.appWrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 5px;
}
.appContent {
    grid-area: c;
    background-color: #ffe760;
    border: 1px solid white;
    border-radius: 25px;
}
